<script>
	/**
	 * written by 张文彧
	 */
	import axios from 'axios'


	export default {
		data() {
			return {
				//是否开始输入，用于提示登录信息
				ifStartEnter: false,
				username: '',
				password: '',
				submitted: false
			};
		},
		computed: {
			notification() {
				return this.$store ? this.$store.state.notification : null;
			}
		},
		created() {
			// document.body.classList.add("auth-body-bg");
		},
		watch: {
			username: function() {
				this.ifStartEnter = true
			},
			password: function() {
				this.ifStartEnter = true
			}
		},
		methods: {
			giveMessage: function(response){
				if(response.data.code == 200){
					this.$message({
							type: 'success',
							message: '登录成功!'
						}),
						//	用户名
						window.sessionStorage.setItem("username",this.username),
						this.selectByUsername()
						this.$router.push({
							path: '/'
						})
				} else{
					console.log(response)
					this.$message({
					    showClose: true,
					    message: '登录失败',
					    ype: 'error'
					});
				}	
			},
			submitForm: function(){
				this.ifStartEnter = true
				if (this.username!='' && this.password!='') {
					/**
					 * ajax请求注册接口
					 */
					axios
						.post('http://82.156.8.5:8848/shiro/login', null, {
							params: {
								username: this.username,
								password: this.password
							}
						})
						.then(response => (
							// this.userList = response.data.list
							this.giveMessage(response)
						))
						.catch(function(error) { // 请求失败处理
							console.log(error);
						});
				}
			},
			selectByUsername: function () {
				axios.get('http://82.156.8.5:8848/users/selectByUsername',
						{params:{'username': window.sessionStorage.getItem('username')}})
						.then(res => {
							console.log(res);
							// 获取到数据, 再将其处理为我们需要的格式
							this.$root.user['user_name'] = res.data.userName;
							this.$root.user['user_password'] = res.data.userPasswrod;
							this.$root.user['user_auth'] = res.data.userAuth;
							this.$root.user['user_status'] = res.data.userStatus;
							this.$root.user['user_pid'] = res.data.pid;
							this.$root.user['user_img_addr'] = res.data.userImgAddr;
							this.$root.user['user_last_login_time'] = res.data.lastLoginTime;
							this.$root.user['user_register_time'] = res.data.registerTime;
							this.$root.user['user_department_id'] = res.data.departmentId;
							this.$root.user['id'] = res.data.id;
							sessionStorage.setItem('id',res.data.id)
							sessionStorage.setItem('auth',res.data.userAuth)
							sessionStorage.setItem('did',res.data.departmentId)
							sessionStorage.setItem('pid',res.data.pid)
							sessionStorage.setItem('username',res.data.userName)
						}).catch(function(error) {
					console.log(error);
				})
			}

		},

	};
</script>　
<template>
	<div>
		<div class="home-btn d-none d-sm-block">
			<a href="/">
				<i class="mdi mdi-home-variant h2 text-white"></i>
			</a>
		</div>
		<div>
			<div class="container-fluid p-0">
				<div class="row no-gutters">
					<div class="col-lg-4">
						<div class="authentication-page-content p-4 d-flex align-items-center min-vh-100">
							<div class="w-100">
								<div class="row justify-content-center">
									<div class="col-lg-9">
										<div>
											<div class="text-center">
												<div>
													<a href="/" class="logo">
														<img src="@/assets/images/logo-dark.png" height="20"
															alt="logo" />
													</a>
												</div>

												<h4 class="font-size-18 mt-4">欢迎回来 !</h4>
												<p class="text-muted">登录后使用云平台</p>
											</div>

											<b-alert variant="danger" class="mt-3" v-if="notification.message" show
												dismissible>{{notification.message}}</b-alert>

											<div class="p-2 mt-5" style="text-align: center;">
												<form class="form-horizontal" @submit.prevent="tryToLogIn">
													<div class="form-group auth-form-group-custom mb-4">
														<i class="ri-mail-line auti-custom-input-icon"></i>
														<label for="email">用户名</label>
														<input v-model="username" class="form-control" id="email"
															placeholder="输入用户名" />
														<div style="text-align: left;" v-if=" ifStartEnter && username==''">
															<el-tag :key="请输入用户名" type="danger">请输入用户名</el-tag>
														</div>
													</div>
														
													<div class="form-group auth-form-group-custom mb-4">
														<i class="ri-lock-2-line auti-custom-input-icon"></i>
														<label for="userpassword">密码</label>
														<input v-model="password" type="password" class="form-control"
															id="userpassword" placeholder="输入密码" />
														<div style="text-align: left;">
															<el-tag v-if="ifStartEnter && password==''" :key="请输入用户名" type="danger">
																请输入用户名</el-tag>
														</div>
													</div>

													<div class="custom-control custom-checkbox">
														<input type="checkbox" class="custom-control-input"
															id="customControlInline" />
														<!-- <label
                              class="custom-control-label"
                              for="customControlInline"
                            >Remember me</label> -->
													</div>


													<el-button class="btn btn-primary w-md waves-effect waves-light" type="primary" @click="submitForm()" >登录
													</el-button>
													<!-- <div class="mt-4 text-center">
                            <router-link tag="a" to="/forgot-password" class="text-muted">
                              <i class="mdi mdi-lock mr-1"></i> 忘记了密码?
                            </router-link>
                          </div> -->
												</form>
											</div>

											<div class="mt-5 text-center">
												<p>
													没有账户?
													<router-link tag="a" to="/register"
														class="font-weight-medium text-primary">点我注册</router-link>
												</p>
												<p>
													© 2021 scdx-cloud
													<i class="mdi mdi-heart text-danger"></i> by scdx
												</p>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div class="col-lg-8">
						<div >
							<div class="bg-overlay"></div>
						</div>
						<!-- <div class="authentication-bg">
							<div class="bg-overlay"></div>
						</div> -->
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
